/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable react/jsx-props-no-spreading */
import React, { useState } from 'react';
import {
  Alert,
  Autocomplete,
  Box,
  Checkbox,
  Button,
  Grid,
  IconButton,
  InputAdornment,
  TextField,
  FormControlLabel,
  Link,
} from '@mui/material';
import { useForm } from 'react-hook-form';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { ApiUserAccount } from 'apitypes';
import UserAccountService from '../../services/user_accounts';
import FormHeading from '../FormHeading/FormHeading';
import styles from './SignUpForm.module.scss';

export default function SingUpForm() {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const [agbChecked, setAgbChecked] = useState<boolean>(false);
  const [datenschutzChecked, setDatenschutzChecked] = useState<boolean>(false);
  const [showPassword, setShowPassword] = useState<Boolean>(false);
  const [showRepeatPassword, setShowRepeatPassword] = useState<Boolean>(false);
  const password = watch('password', '');
  const navigate = useNavigate();
  const [submitError, setSubmitError] = useState<string>('');

  const onSubmit = (data: any) => {
    if (!agbChecked || !datenschutzChecked) {
      setSubmitError('Bitte die AGB und Datenschutzbestimmungen akzeptieren');
      return;
    }

    const apiUser: ApiUserAccount = {
      title: data.title,
      firstname: data.firstname,
      lastname: data.lastname,
      email: data.email,
      password: data.password,
    };

    UserAccountService.addUserAccount(apiUser)
      .then((res) => {
        if (res.status === 201) {
          setSubmitError('');
          navigate('/welcome', { state: { email: data.email } });
        } else if (res.status === 409) {
          setSubmitError('E-Mail ist bereits registriert');
        } else if (res.status === 400) {
          setSubmitError('Ungültige Eingabe');
        } else if (res.status === 500) {
          setSubmitError('Verarbeitungsfehler auf dem Server');
        } else {
          setSubmitError(`Fehler bei der Verbindung zum Server: ${res.status}`);
        }
      })
      .catch((error) => {
        setSubmitError(`Fehler bei der Verbindung zum Server: ${error}`);
      });
  };

  const titleOptions: string[] = ['Herr', 'Frau', 'keine Angabe'];

  return (
    <Box component="form" onSubmit={handleSubmit(onSubmit)}>
      <FormHeading>Registrierung</FormHeading>
      <Grid container justifyContent="center" spacing={2} paddingX={3}>
        <Grid item xs={12}>
          <Autocomplete
            fullWidth
            renderInput={(params) => (
              <TextField
                {...params}
                {...register('title', {
                  required: 'Pflichtfeld',
                })}
                label="Anrede*"
                error={errors.title !== undefined}
                helperText={errors.title?.message?.toString()}
                data-testid="signup_title"
              />
            )}
            options={titleOptions}
            id="anrede"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            {...register('firstname', { required: 'Pflichtfeld' })}
            label="Vorname*"
            autoComplete="firstname"
            error={errors.firstname !== undefined}
            helperText={errors.firstname?.message?.toString()}
            data-testid="signup_firstname"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            {...register('lastname', { required: 'Pflichtfeld' })}
            label="Nachname*"
            autoComplete="lastname"
            error={errors.lastname !== undefined}
            helperText={errors.lastname?.message?.toString()}
            data-testid="signup_lastname"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            {...register('email', {
              required: 'Pflichtfeld',
              pattern: {
                value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                message: 'Ungültige E-Mail Adresse',
              },
            })}
            label="E-Mail Adresse*"
            autoComplete="email"
            error={errors.email !== undefined}
            helperText={errors.email?.message?.toString()}
            data-testid="signup_email"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            type={showPassword ? 'text' : 'password'}
            fullWidth
            {...register('password', {
              required: 'Pflichtfeld',
              pattern: {
                value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
                message: 'Passwort muss aus mindestens 8 Zeichen (je mindestens 1 Großbuchstabe, Kleinbuchstabe, Nummer, Sonderzeichen) bestehen',
              },
            })}
            label="Passwort*"
            autoComplete="password"
            error={errors.password !== undefined}
            helperText={errors.password === undefined
              ? 'Passwort muss aus mindestens 8 Zeichen (je mindestens 1 Großbuchstabe, Kleinbuchstabe, Nummer, Sonderzeichen) bestehen'
              : errors.password?.message?.toString()}
            InputProps={{
              endAdornment:
              (
                <InputAdornment position="end">
                  <IconButton
                    aria-label={showPassword ? 'Passwort verstecken' : 'Passwort zeigen'}
                    onClick={() => setShowPassword(!showPassword)}
                    onMouseDown={(e) => e.preventDefault()}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
            data-testid="signup_password"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            type={showRepeatPassword ? 'text' : 'password'}
            fullWidth
            {...register('passwordRepeat', {
              required: 'Pflichtfeld',
              validate: (val) => val === password || 'Passwörter stimmen nicht überein',
            })}
            label="Passwort wiederholt*"
            autoComplete="passwordRepeat"
            error={errors.passwordRepeat !== undefined}
            helperText={errors.passwordRepeat?.message?.toString()}
            data-testid="signup_password_repeat"
            InputProps={{
              endAdornment:
              (
                <InputAdornment position="end">
                  <IconButton
                    aria-label={showRepeatPassword ? 'Passwort verstecken' : 'Passwort zeigen'}
                    onClick={() => setShowRepeatPassword(!showRepeatPassword)}
                    onMouseDown={(e) => e.preventDefault()}
                    edge="end"
                  >
                    {showRepeatPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={(
              <Checkbox
                checked={agbChecked}
                onChange={() => setAgbChecked(!agbChecked)}
              />
            )}
            label={(
              <span>
                Ich akzeptiere die <Link href="/">AGB</Link>
              </span>
            )}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={(
              <Checkbox
                checked={datenschutzChecked}
                onChange={() => setDatenschutzChecked(!datenschutzChecked)}
              />
            )}
            label={(
              <span>
                Ich akzeptiere die <Link href="/">Datenschutzbestimmung</Link> und willige in die Kontaktaufnahme per Email ein
              </span>
            )}
          />
        </Grid>
        {submitError ? (
          <Grid item xs={12} textAlign="center">
            <Alert severity="error">
              {submitError}
            </Alert>
          </Grid>
        ) : ''}
      </Grid>
      <div className={styles.linkContainer}>
        <Button sx={{ marginBottom: 1.5 }} type="submit" variant="contained" data-testid="signup_submit">
          Registrieren
        </Button>
        <span> Bereits angemeldet?
          <a href="/" className={styles.link}> Login</a>
        </span>
      </div>
    </Box>
  );
}
